
.domain-mute-card {
  flex: 1 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.6em 1em 0.6em 0;

  &-domain {
    margin-right: 1em;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  button {
    width: 10em;
  }

  .autosuggest-results & {
    padding-left: 1em;
  }
}
