
.mfa-settings {
  .mfa-heading,
  .method-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: baseline;
  }

  .warning {
    color: var(--cOrange);
  }

  .setup-otp {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    .qr-code {
      flex: 1;
      padding-right: 10px;
    }
    .verify { flex: 1; }
    .error { margin: 4px 0 0; }

    .confirm-otp-actions {
      button {
        width: 15em;
        margin-top: 5px;
      }
    }
  }
}
