
.theme-preview-container {
  position: relative;
  border-top: 1px dashed;
  border-bottom: 1px dashed;
  border-color: var(--border);
  margin: 1em 0;
  padding: 1em;
  background-color: var(--wallpaper);
  background-image: var(--body-background-image);
  background-size: cover;
  background-position: 50% 50%;

  .theme-preview-content {
    padding: 20px;
  }

  .dummy {
    .post {
      font-family: var(--postFont);
      display: flex;

      .content {
        flex: 1;

        h4 {
          margin-bottom: 0.25em;
        }

        .icons {
          margin-top: 0.5em;
          display: flex;

          i {
            margin-right: 1em;
          }
        }
      }
    }

    .after-post {
      margin-top: 1em;
      display: flex;
      align-items: center;
    }

    .avatar,
    .avatar-alt {
      background:
        linear-gradient(
          135deg,
          #b8e1fc 0%,
          #a9d2f3 10%,
          #90bae4 25%,
          #90bcea 37%,
          #90bff0 50%,
          #6ba8e5 51%,
          #a2daf5 83%,
          #bdf3fd 100%
        );
      color: black;
      font-family: sans-serif;
      text-align: center;
      margin-right: 1em;
    }

    .avatar-alt {
      flex: 0 auto;
      margin-left: 28px;
      font-size: 12px;
      min-width: 20px;
      min-height: 20px;
      line-height: 20px;
    }

    .avatar {
      flex: 0 auto;
      width: 48px;
      height: 48px;
      font-size: 14px;
      line-height: 48px;
    }

    .actions {
      display: flex;
      align-items: baseline;

      .checkbox {
        margin-right: 1em;
        flex: 1;
      }
    }

    .separator {
      margin: 1em;
      border-bottom: 1px solid;
      border-color: var(--border);
    }

    .btn {
      min-width: 3em;
    }
  }

  .underlay-preview {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 10px;
    right: 10px;
  }
}
 