
.autosuggest {
  position: relative;

  &-input {
    display: block;
    width: 100%;
  }

  &-results {
    position: absolute;
    left: 0;
    top: 100%;
    right: 0;
    max-height: 400px;
    background-color: var(--bg);
    border-style: solid;
    border-width: 1px;
    border-color: var(--border);
    border-radius: var(--roundness);
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    box-shadow: 1px 1px 4px rgb(0 0 0 / 60%);
    box-shadow: var(--shadow);
    overflow-y: auto;
    z-index: 1;
  }
}
