
.shadow-control {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 1em;

  .shadow-preview-container,
  .shadow-tweak {
    margin: 5px 6px 0 0;
  }

  .shadow-preview-container {
    flex: 0;
    display: flex;
    flex-wrap: wrap;

    input[type="number"] {
      width: 5em;
      min-width: 2em;
    }

    .x-shift-control,
    .y-shift-control {
      display: flex;
      flex: 0;

      &[disabled="disabled"] * {
        opacity: 0.5;
      }
    }

    .x-shift-control {
      align-items: flex-start;
    }

    .x-shift-control .wrap,
    input[type="range"] {
      margin: 0;
      width: 15em;
      height: 2em;
    }

    .y-shift-control {
      flex-direction: column;
      align-items: flex-end;

      .wrap {
        width: 2em;
        height: 15em;
      }

      input[type="range"] {
        transform-origin: 1em 1em;
        transform: rotate(90deg);
      }
    }

    .preview-window {
      flex: 1;
      background-color: #999;
      display: flex;
      align-items: center;
      justify-content: center;
      background-image:
        linear-gradient(45deg, #666 25%, transparent 25%),
        linear-gradient(-45deg, #666 25%, transparent 25%),
        linear-gradient(45deg, transparent 75%, #666 75%),
        linear-gradient(-45deg, transparent 75%, #666 75%);
      background-size: 20px 20px;
      background-position: 0 0, 0 10px, 10px -10px, -10px 0;
      border-radius: var(--roundness);

      .preview-block {
        width: 33%;
        height: 33%;
        border-radius: var(--roundness);
      }
    }
  }

  .shadow-tweak {
    flex: 1;
    min-width: 280px;

    .id-control {
      align-items: stretch;

      .shadow-switcher {
        flex: 1;
      }

      .shadow-switcher,
      .btn {
        min-width: 1px;
        margin-right: 5px;
      }

      .btn {
        padding: 0 0.4em;
        margin: 0 0.1em;
      }
    }
  }
}
